/* common style */

.width {
    width: 100%;
}

.body {
    width: 100%;
    height: 100%;
    background-color: #FFFBD1;
}

/* Top section */
section {
    width: 100%;

    height: 971px;
}

.logoarea {
    width: 100%;
    height: 150px;
}

.logo {
    width: 45%;
    height: 100%;
    margin-left: 27%;

}

.top {
    width: 100%;

    display: flex;

}


/* left side image */
.leftSide {
    width: 30%;


}

.awardimg {
    width: 100%;
    height: 100%;

}

/* Right side content */

.rightSide {

    width: 69%;

}

.content1 .p {
    font-weight: 900;
}


.img2area {
    width: 100%;
}

.img2 {
    width: 100%;
    height: 100%;
}

.para span {
    font-weight: 900;
}

/* Middle part of site */
.center1 {
    width: 100%;
    height: 20px;
    font-size: 16px;
    text-align: center;
}


.img3area {
    width: 84%;
    height: 300px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 1%;
}

.img3 {
    width: 100%;
    height: 100%;
}

.textarea {
    width: 100%;
    height: 45px;
}


.textarea p {
    text-align: center;
    margin: 0;
    margin-top: 1%;

}

.redLine {
    width: 95%;
    height: 2px;
    background-color: red;
    margin-left: auto;
    margin-right: auto;
}

/* Down part */
.cri {
    width: 100%;
}

.cri p {
    text-align: center;
}

.provide {
    width: 90%;
    margin: 0 auto 0 auto;
}

.provide span {
    width: 100%;
}

.provide span span {
    color: red;
}

/* Footer */
.footer {
    width: 95%;
    background-color: #EC3237;
    margin: 3px auto 0 auto;
    display: flex;
}

.section {
    align-content: center;
    padding-left: 12%;

    display: flex;
    width: 33%;
    text-align: center;
    align-items: center;
}

.iconarea {
    display: inline-block;
    width: 7%;
    height: 30px;

}

.icon {
    width: 100%;
    height: 100%;

}

.section p {
    display: inline-block;
}

/* Responsive mobile side  */
@media only screen and (max-width: 572px) {
    .body {
        width: 100%;
        height: 100%;
    }

    .logoarea {
        width: 100%;

    }

    .logo {

        width: 100%;
        height: 100%;
        margin-left: 0%;


    }

    .leftSide {
        width: 42%;
        text-align: center;
        margin-left: auto;
        margin-right: auto;
    }

    .top,
    .row {
        display: block;
    }

    .rightSide {
        width: 100%;
    }

    .content1 .p {
        text-align: center;
    }

    .content1 {
        height: 400px;
    }

    .img2area {
        width: 98%;
    }

    .img3area {
        width: 78%;
        height: 124px;

    }

    .rightSide {
        width: 98%;
    }

    .center1 {


        height: 100px;
        margin-top:32%;
    }

    .img2area {
        width: 98%;
    }

    .img3area {
        width: 78%;
        height: 124px;

    }

    

    .para {

        width: 98%;
        height: 123px;

        margin-left: 2%;

    }

    .footer {
        width: 100%;
        background-color: #EC3237;
        display: block;
        padding: 0;
        
       
    }
    .section {
        display: block;

        width: 100%;
        height: 39px;
        padding: 0;
    }
    .iconarea {
        display: inline-flex;
        width: 7%;
        height: 30px;

    }

    .icon {
        width: 100%;
        height: 100%;

    }
    .iconarea p{
        display: inline-flex;
        font-size: 21px;
    }

}